import React, { useState } from 'react';
import './App.css';
// @ts-ignore
import { motion } from "framer-motion";

const variants = {
    hidden: { opacity: 0 },
    visible: { opacity: 1 },
};

// @ts-ignore
const Form = ({ setIframe }) => {
    const [word, setWord] = useState('');
    const [textColor, setTextColor] = useState('#dfff59');
    const [error, setError] = useState(false);

    const onTestClick = () => {
        if (!word) {
            setError(true);
            return;
        }

        setIframe(word, textColor.slice(1).toUpperCase());
    }

    const onWordChange = (e: { target: { value: React.SetStateAction<string>; }; }) => {
        setWord(e.target.value);
        setError(!e.target.value);
    }

    return (
        <motion.div initial="hidden" animate="visible" variants={variants} transition={{ duration: 2 }}>
            <label>
                –ü–æ–∏–≥—Ä–∞–µ–º –≤ —Å–ª–æ–≤–∞. –ï—Å–ª–∏ –±—ã —Ç—ã –º–æ–≥ –æ–ø–∏—Å–∞—Ç—å —Å–≤–æ—ë —Å–æ—Å—Ç–æ—è–Ω–∏–µ –æ–¥–Ω–∏–º —Å–ª–æ–≤–æ–º, —á—Ç–æ —ç—Ç–æ –±—ã–ª–æ –±—ã –∑–∞ —Å–ª–æ–≤–æ?
                –í—ã–±–µ—Ä–∏ —Å–ª–æ–≤–æ, –∫–æ—Ç–æ—Ä—ã–º –±—ã —Ç—ã –æ–ø–∏—Å–∞–ª —Å–≤–æ—ë –±—É–¥—É—â–µ–µ.
                –í—ã–±–µ—Ä–∏ —Å–ª–æ–≤–æ, –∫–æ—Ç–æ—Ä–æ–µ —Ç–µ–±—è –≤–¥–æ—Ö–Ω–æ–≤–ª—è–µ—Ç! –≠—Ç–æ —Å–ª–æ–≤–æ, –∫–æ—Ç–æ—Ä–æ–µ —Ç–µ–±–µ –Ω—Ä–∞–≤–∏—Ç—Å—è. –û–Ω–æ –∫—Ä–∞—Å–∏–≤–æ–µ. –¢–µ–±–µ –Ω—Ä–∞–≤–∏—Ç—Å—è –µ–≥–æ —Å–ª—ã—à–∞—Ç—å, –ø—Ä–æ–∏–∑–Ω–æ—Å–∏—Ç—å, –≤–∏–¥–µ—Ç—å –≤ –∫–Ω–∏–≥–∞—Ö –∏–ª–∏ –≤ —Å–æ–æ–±—â–µ–Ω–∏—è—Ö –æ—Ç –¥—Ä—É–∑–µ–π... –û–Ω–æ –∑–∞—Ä—è–∂–∞–µ—Ç –∏ –º–æ—Ç–∏–≤–∏—Ä—É–µ—Ç. –ü–æ–¥–µ–ª–∏—Å—å —Å –Ω–∞–º–∏ —ç—Ç–∏–º —Å–ª–æ–≤–æ–º. –ü—Ä—è–º–æ –∑–¥–µ—Å—å. –ü—Ä—è–º–æ —Å–µ–π—á–∞—Å
                <input
                    key='word'
                    type='text'
                    onChange={onWordChange}
                    required
                    className={error ? 'error' : 'valid'}
                    defaultValue={word}
                />
            </label>
            <label>
                –ù–µ–ø–ª–æ—Ö–æ. –ö–∞–∫ –Ω–∞—Å—á—ë—Ç —Ü–≤–µ—Ç–æ–≤? –ö–æ–≥–¥–∞ —Ç—ã –≤–∏–¥–∏—à—å —ç—Ç–æ —Å–ª–æ–≤–æ. –° –∫–∞–∫–∏–º —Ü–≤–µ—Ç–æ–º –æ–Ω–æ –∞—Å—Å–æ—Ü–∏–∏—Ä—É–µ—Ç—Å—è?
                <input key='color' type='color' onChange={(e) => setTextColor(e.target.value)} defaultValue='#dfff59' />
            </label>
            <button onClick={onTestClick}>–î–∞–ª—å—à–µ</button>
        </motion.div>
    );
}

export default Form;
