import React, { useState } from 'react';
// @ts-ignore
import logo from './DAN.JPG';
import './App.css';
// @ts-ignore
import Step from "./Step.tsx";
import FormStep from "./FormStep";

const steps: any[] = [{
    texts: [
        '–î–∞–Ω–∏–∏–ª!',
        '–û–±—â–µ—Å—Ç–≤–µ–Ω–Ω–æ–µ –¥–≤–∏–∂–µ–Ω–∏–µ –î–∞–ù–∞–°—Ä–∞ –ø–æ–∑–¥—Ä–∞–≤–ª—è–µ—Ç –í–∞—Å —Å –î–Ω—ë–º —Ä–æ–∂–¥–µ–Ω–∏—è.'
    ],
    img: logo
},
{
    img: logo,
    texts: ['–î–ª—è –Ω–∞—á–∞–ª–∞ –º—ã —Ö–æ—Ç–∏–º —Å–∫–∞–∑–∞—Ç—å —Ç–µ–±–µ –Ω–µ—Å–∫–æ–ª—å–∫–æ —Ç—ë–ø–ª—ã—Ö —Å–ª–æ–≤.', '–í–æ-–ø–µ—Ä–≤—ã—Ö, –º—ã —Ç–µ–±—è –ª—é–±–∏–º.', '–í–æ-–≤—Ç–æ—Ä—ã—Ö, –∫–∞–∂–¥–æ–º—É –∏–∑ –Ω–∞—Å –µ—Å—Ç—å —á—Ç–æ —Ç–µ–±–µ —Å–∫–∞–∑–∞—Ç—å.']
}, {
    img: logo,
    texts: ['–¢–µ–∫—Å—Ç –æ—Ç –ê–Ω–∏']
}, {
    img: logo,
    texts: ['–¢–µ–∫—Å—Ç –æ—Ç –†–æ–±–µ—Ä—Ç–∞']
}, {
    img: logo,
    texts: ['–¢–µ–∫—Å—Ç –æ—Ç –ù–∞—Ç–∞—à–∏']
}, {
    img: logo,
    texts: ['–¢–µ–∫—Å—Ç –æ—Ç –°–∞–Ω–∏']
}, {
    img: logo,
    texts: [
        '–ö–æ–Ω–µ—á–Ω–æ, —Ç—ã –∑–Ω–∞–µ—à—å, —á—Ç–æ –º—ã –Ω–µ —Å—Ç–∞–Ω–µ–º –ø—Ä–æ—Å—Ç–æ —Ç–∞–∫ –¥–∞—Ä–∏—Ç—å —Ç–µ–±–µ –ø–æ–¥–∞—Ä–æ–∫. –ü—Ä–æ—Å—Ç–æ–π –ø–æ–¥–∞—Ä–æ–∫. –ù–∞–ø—Ä–∏–º–µ—Ä, –ø—Ä—ã–∂–æ–∫ —Å –ø–∞—Ä–∞—à—é—Ç–æ–º –∏–ª–∏ –Ω–æ—Å–∫–∏ - —ç—Ç–æ –≤—Å–µ –±–∞–Ω–∞–ª—å–Ω–æ. –í–æ–æ–±—â–µ —ç—Ç–æ –Ω–µ –ø–æ–¥–∞—Ä–æ–∫, —ç—Ç–æ –ø–æ–∑–¥—Ä–∞–≤–ª–µ–Ω–∏–µ. –î–∞ –∏ –Ω–µ –ø–æ–∑–¥—Ä–∞–≤–ª–µ–Ω–∏–µ, –∞ –ø—Ä–∏—è—Ç–Ω—ã–µ —Å–ª–æ–≤–∞ —Ç–µ–±–µ. –¢—ã —É—Å–ø–µ—à–µ–Ω –≤ —Ä–∞–±–æ—Ç–µ, –≤ —Å–∞–º–æ—Ä–∞–∑–≤–∏—Ç–∏–∏, —Ç—ã –æ—á–µ–Ω—å –º–Ω–æ–≥–æ –¥—É–º–∞–µ—à—å. –ü–æ—Å—Ç–æ—è–Ω–Ω–æ. –ü–ª–∏–∑, —Å–µ–≥–æ–¥–Ω—è –ø—Ä–æ—Å—Ç–æ —Ä–∞—Å—Å–ª–∞–±—å—Å—è –∏ –Ω–µ –ø—ã—Ç–∞–π—Å—è —Ä–∞—Ü–∏–æ–Ω–∞–ª–∏–∑–∏—Ä–æ–≤–∞—Ç—å. –¢—ã –≤ —Ä—É–∫–∞—Ö –Ω–∞–¥–µ–∂–Ω—ã—Ö –¥—Ä—É–∑–µ–π, –Ω—É —Ç—ã —É–∂–µ —Å–∞–º –≤—Å–µ –ø–æ–Ω—è–ª. –í—Å–µ, —á—Ç–æ —Ç–µ–±–µ –Ω–∞–¥–æ —Å–¥–µ–ª–∞—Ç—å - —ç—Ç–æ –∑–∞–∫—Ä—ã—Ç—å –≥–ª–∞–∑–∞... –Ω–æ –Ω–µ —Å–µ–π—á–∞—Å. –°–Ω–∞—á–∞–ª–∞ –¥–æ—á–∏—Ç–∞–π.'
    ]
}, {
    img: logo,
    texts: [
        '–¢–µ–±–µ —Å–µ–≥–æ–¥–Ω—è 24. –°–∫–æ–ª—å–∫–æ —Ç—ã —É—Å–ø–µ–ª? –ß—Ç–æ –æ–∂–∏–¥–∞–ª –æ—Ç —ç—Ç–æ–≥–æ –≤–æ–∑—Ä–∞—Å—Ç–∞? –ß—Ç–æ –ø–æ–ª—É—á–∏–ª–æ—Å—å? –ö–∞–∂–µ—Ç—Å—è, –ø–æ–ª—É—á–∏–ª–æ—Å—å –º–Ω–æ–≥–æ–µ) –ö–∞–∫ —Ç—ã —Å–µ–±—è –æ—â—É—â–∞–µ—à—å?'
    ]
}];
let fetched = false;

const App: React.FC = () => {
    const [step, setStep] = useState(0);
    const [word, setWord] = useState('');
    const [textColor, setColor] = useState('000000');

    if (!fetched) {
        fetch('http://kirdan.ru', {
            mode: 'cors',
            method: 'post',
        })
            .then(async data => {
                const res = await data.json();

                fetched = true;
                setWord(res.image_text);
                setColor(res.image_text_color);
            })
            .catch(() => {
            });
    }

    const nextStep = () => {
        setStep(step + 1);
    };

    return <div className="App">
        <main>
            {step === steps.length ?
                <FormStep wordApi={word} textColorApi={textColor}/> :
                <Step data={steps[step]} handleClick={nextStep} key={step} />
            }
        </main>
        <footer>Created by danasra (c) 2019</footer>
    </div>;
}

export default App;
